import React from "react";
import "./Recipe.css";

const Recipe = (props) => {
  if (props.recipe != null) {
    console.log(props);
    return (
      <div className="recipe_wrapper">
        {props.recipe.map((element, index) => {
          return (
            <div key={index} className="recipe_itself">
              <img
                src={element.recipe.image}
                width={200}
                height={200}
                alt={element.recipe.label}
                className="recipe_image"
              />
              <div className="recipe_info">
                <div>
                  <div className="recipe_label">{element.recipe.label}</div>
                  <div className="recipe_calories">
                    Calories : {Math.round(Number(element.recipe.calories))}
                  </div>
                  <div className="recipe_calories">
                    Total Weight :{" "}
                    {Math.round(Number(element.recipe.totalWeight))}gm
                  </div>
                  <div className="recipe_calories">
                    Cuisine Type : {element.recipe.cuisineType[0]}
                  </div>
                  <div className="recipe_calories">
                    Dish Type : {element.recipe.dishType[0]}
                  </div>
                </div>
                <a
                  href={element.recipe.url}
                  className="recipe_link"
                  target="_blank"
                >
                  Click to know the full recipe
                </a>
              </div>
            </div>
          );
        })}
      </div>
    );
  } else {
    return <div>no, way</div>;
  }
};

export default Recipe;
