import "./styles.css";
import axios from "axios";
import React, { useState, useEffect } from "react";
import Recipe from "./Recipe";

export default function App() {
  const [recipes, setRecipes] = useState([]);
  const [query_recipe, setQuery_Recipe] = useState("Pork");

  useEffect(() => {
    async function getData() {
      var options = {
        method: "GET",
        url: "https://edamam-recipe-search.p.rapidapi.com/search",
        params: { q: query_recipe },
        headers: {
          "x-rapidapi-host": "edamam-recipe-search.p.rapidapi.com",
          "x-rapidapi-key": "ab2cceea62mshb6e085c0ad6b68ap141bbbjsn560c5b3033a7"
        }
      };

      axios
        .request(options)
        .then(function (response) {
          //console.log(response.data);
          setRecipes(response.data);
        })
        .catch(function (error) {
          console.error(error);
        });
    }
    getData();
    //console.log(recipes);
  }, []);

  const handleSearch = () => {
    async function getData() {
      var options = {
        method: "GET",
        url: "https://edamam-recipe-search.p.rapidapi.com/search",
        params: { q: query_recipe },
        headers: {
          "x-rapidapi-host": "edamam-recipe-search.p.rapidapi.com",
          "x-rapidapi-key": "ab2cceea62mshb6e085c0ad6b68ap141bbbjsn560c5b3033a7"
        }
      };

      axios
        .request(options)
        .then(function (response) {
          console.log(response.data);
          setRecipes(response.data);
        })
        .catch(function (error) {
          console.error(error);
        });
    }
    getData();
    setQuery_Recipe("");
  };

  return (
    <div className="first_div">
      <div className="App_Bar">
        <div className="App_Bar_title">Recipe Master</div>
        <div className="App_Bar_Search">
          <input
            placeholder="Type in ingredient / Recipe Name"
            value={query_recipe}
            onChange={(e) => setQuery_Recipe(e.target.value)}
          />
          <button onClick={handleSearch}>Search</button>
        </div>
      </div>
      {recipes !== null ? (
        <Recipe recipe={recipes.hits} />
      ) : (
        <div>There are no recipes</div>
      )}
      <div className="App_Footer">
        <div className="part_one">Home | Recipes | SiteMap</div>
        <div className="part_second">2021 @ copyright reserved</div>
      </div>
    </div>
  );
}
